html {
  font-family: Roboto, sans-serif;
}

.menu {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.menu__logo {
  display: flex;
}

.nav {
  font-weight: 500;
}

.nav__list {
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 690px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list .video {
  margin: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  font-weight: 500;
  transition-duration: .3s;
}

.is-active {
  color: #00acdc;
  font-weight: 500;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px 19px 28px;
}

.card__title {
  color: #060b35;
  margin: 8px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
  transition-duration: .3s;
}

.card__code {
  color: #616070;
  margin: 4px 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  text-align: center;
  justify-content: space-between;
  justify-items: center;
  margin: 16px;
  display: flex;
}

.card__review--score {
  color: #060b35;
  justify-content: flex-end;
  height: 16px;
  margin: 0;
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  max-width: 100%;
  margin: 24px 16px 0;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-number {
  color: #060b35;
  justify-content: flex-end;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  width: 166px;
  margin: 16px 16px 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: inline-block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card-container {
  box-sizing: border-box;
  grid-template-rows: 408px 408px;
  justify-content: center;
  gap: 46px 48px;
  margin: auto;
  padding-top: 50px;
  display: grid;
}

@media (width <= 488px) {
  .card-container {
    grid-template-columns: 200px;
  }
}

@media (width >= 488px) {
  .card-container {
    grid-template-columns: 200px 200px;
  }
}

@media (width >= 768px) {
  .card-container {
    grid-template-columns: 200px 200px 200px;
  }
}

@media (width >= 1024px) {
  .card-container {
    grid-template-columns: 200px 200px 200px 200px;
  }
}

body, div, p {
  margin: 0;
}

